.preloader {
  position: fixed;
  left: 0;
  width: 100%;
  height: 100%;
  top: 0px;
  background: #000;
  z-index: 5;
  transition: 1s all;
  opacity: 1;
  visibility: visible;
}
.preloader .loader {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 20px solid white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 70px;
}
.preloader .loader h3 {
  color: white;
  font-size: 30px;
  font-weight: 700;
}
.preloader .logo {
  color: white;
  font-size: 36px;
  font-weight: 900;
  text-decoration: none;
  transition: 1ms;
  cursor: pointer;
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.preloader .logo span {
  color: #ff414e;
}
.button_top {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #000;
  position: fixed;
  top: 300px;
  right: 100px;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 500ms;
  visibility: hidden;
  cursor: pointer;
}
@media (max-width: 576px) {
  .button_top {
    width: 30px;
    height: 30px;
    right: 10px;
    font-size: 15px;
  }
}
@media (max-width: 992px) {
  .button_top {
    right: 40px;
  }
}
.button_top:hover {
  box-shadow: 2px 2px 10px black;
  color: #ff414e;
}
.button_top:hover i {
  color: #ff414e;
}
.button_top ul {
  position: fixed;
  top: 250px;
  right: 95px;
}
@media (max-width: 576px) {
  .button_top ul {
    right: 5px;
    font-size: 15px;
  }
}
@media (max-width: 992px) {
  .button_top ul {
    right: 40px;
  }
}
.button_top ul li {
  margin: 10px 0px;
}
.button_top ul li .texting {
  display: none;
  color: #ff414e;
  font-size: 18px;
  text-transform: capitalize;
  font-weight: 700;
  position: relative;
}
@media (max-width: 576px) {
  .button_top ul li .texting {
    font-size: 15px;
  }
}
.button_top ul li .texting::after {
  content: "";
  display: block;
  width: 100%;
  height: 4px;
  background: black;
  box-shadow: 4px 4px 8px black;
  margin-top: 3px;
}
.button_top ul li .texting::before {
  content: "";
  display: inline-flex;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: black;
  box-shadow: 4px 4px 8px black;
  margin-top: 3px;
  position: absolute;
  left: -30px;
}
@media (max-width: 576px) {
  .button_top ul li .texting::before {
    display: none;
  }
}
@media (max-width: 992px) {
  .button_top ul li .texting::before {
    display: none;
  }
}
